.testimonials-grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    padding: 10px;
}

.tcards{
    border: solid thin lightgrey;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    -moz-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    padding: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-top: 30px;
    padding-bottom: 30px;
}

.quote-color{
    color: lightblue;
}

.ttext{
    padding-top: 2%;
    padding-left: 10%;
    padding-right: 10%;
    padding-bottom: 2%;
    text-align: center;
}

.tby{
    text-align: center;
}


