.gallary-banner{
    -webkit-box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
            box-shadow: 0px 0px 20px rgba(0,0,0,0.2);   
}

.gallary-banner img{
    width: 100%;
}


.photo-card{
    border: solid thin lightgrey;
    border-radius: 5px;
    background-color: white;
    -webkit-box-shadow: 0px 0px 20px rgba(0,0,0,0.2);
            box-shadow: 0px 0px 20px rgba(0,0,0,0.2);   
}

.photocard-image{
    width: 100%;
}

.photocard-text{
    padding: 10px;
}


.gallary-others{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
}


