.school{
    margin-top: 20vh;
    padding-left: 15vh;
    padding-right: 15vh; 
}

@media only screen and (max-width: 600px){
    .school{
        margin-top: 10vh !important;
        padding-left: 3vh !important;
        padding-right: 3vh !important;
    }

    .school header p{
        font-size: 1em !important;
    }

    .school-content{
        grid-template-columns: 1fr !important; 
    }

    .left-image{
        padding-top: 0px !important;
        text-align: center;
    }

    .image-left{
        position: relative !important;
    }
    .right-text{
        margin-top: -30px !important;
        padding-top: 40px !important;
    }

    .right-image{
        margin-top: -80px !important;
        text-align: center;
        padding-bottom: 50px !important;
    }

    .image-right{
        position: relative !important;
    }
    
}

.school header{
    text-align: center;
}

.school header h1{
    font-size: 2.44em;
    font-weight: 900;
}

.school-content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    min-height: 80vh;
    padding-top: 10vh;
}

.school header p{
    font-size: 1.5em;
    font-weight: 300;
    padding-left: 10vw;
    padding-right: 10vw;
}

.left-image{
    padding-top: 50px;
}


.image-left{
    position: absolute;
    width: 55%;
}

.right-text{
    background-color: #f9f9f9;
    padding: 30px;
    padding-left: 7vw;
}

.right-text p{
    text-align: justify;
    line-height: 2em;
}

.right-text h5{
    color: #0071c5;
    font-size: 1.2em;
}


/* ****************** */


.right-image{
    padding-top: 50px;
}


.image-right{
    position: absolute;
    width: 55%;
    margin-left: -4vw;

}

.left-text{
    background-color: #f9f9f9;
    padding: 30px;
    padding-right: 7vw;
}

.left-text p{
    text-align: justify;
    line-height: 2em;
}

.left-text h5{
    color: #0071c5;
    font-size: 1.2em;
}


.quoted-text-tag{
    font-size: 1.2em;
    font-style: italic;
}

/* ***************** */




