.collapsible {
    background-color: #e4e3e3;
    color: #2f2f2f;
    cursor: pointer;
    padding: 18px;
    border: none;
    width: 100%;
    text-align: left;
    outline: none;
    font-size: 15px;
    font-weight: 600;
}

.content {
    display: none;
    overflow: hidden;
    background-color: #f1f1f1;
}

.coll{
    margin-bottom: 20px;
}


