.campuslife-sections{
    margin-top: 20vh;
    padding: 20px;
}

.campuslife-sections header{
    text-align: center;
}

.campuslife-sections header h1{
    font-size: 2.44em;
    font-weight: 900;
    padding-bottom: 20px;
}


.campuslife-sections header p{
    font-size: 1.5em;
    padding-left: 20vw; 
    padding-right: 20vw; 
}

.campuslife-body{
    padding-top: 30px;
}
.campuslife-body img{
    width: 90%;
}

.three-grid{
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

@media only screen and (max-width: 600px){
    .three-grid{
        grid-template-columns: 1fr;
        grid-row-gap: 30px;
    }
}

.campus-grid-comp{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.campus-grid-comp-image img{
    width: 100%;
}

.campus-grid-comp-contents{
    text-align: left;
}

.campus-grid-comp-contents h2{
    padding-top: 10px;
    padding-bottom: 20px;
}

.campus-grid-comp-contents p{
    text-align:  justify;
    line-height: 1.8em;
}


.antiragging{
    background-color:#06152c;
    min-height: 86vh;
    padding: 30px;
    
}

.antiragging header{
    text-align: center;
    color: white;
}

.antiragging header h1{
    font-size: 2.44em;
    font-weight: 900;
}


.antiragging header p{
    font-size: 1.5em;
    padding-left: 20vw; 
    padding-right: 20vw; 
}




.campus-life-sections header{
    text-align: center;
}

.campus-life-sections header h1{
    font-size: 2.44em;
    font-weight: 900;
}


.campus-life-sections header p{
    font-size: 1.5em;
    padding-left: 20vw; 
    padding-right: 20vw; 
}

.bglight{
    background-color: #f2f2f2;
}


/* *********************************** */

.quoted-text-tag-1{
    font-size: 1.1em;
    font-weight: 400;
    font-style: italic;
}


