.navbar_1{
    background-color: #0071c5;
    width: 100%;
    padding-left: 6vw;
    color: white;
    font-weight: 500;
    height: 5vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.navbar_1_header{
    background-color: #fcfcfc;
    width: 90%;
    height: 15vh;
    padding-left: 6vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
}

.navbar_1_logo{
    -ms-flex-preferred-size: 10%;
        flex-basis: 10%;
    text-align: center;
    border-right: solid 2px #f2f2f2;
}


.navbar_1_headertext{
    line-height: 1.9em;
    overflow-y: hidden;
    padding-left: 40px;
}


.make-italics{
    font-size: 1.5em ;
    font-weight: 300;
    font-style: italic;
}

.make-headertext{
    font-size: 2.5em;
    font-style: italic;
}

.aec-logo{
    width: 45%;
}

@media only screen and (max-width: 600px){
    .aec-logo{
        width: 100%;
    }

    .navbar_1_headertext{
        font-size: 0.7em !important;
    }

    .navbar_1_logo{
        -ms-flex-preferred-size: 15%;
            flex-basis: 15%;
    }
    .make-headertext{
        font-size: 1.2em !important;
        font-weight: 800;
    }

    .navbar_1_headertext{
        padding-left: 20px !important;
    }

    .navbar_1_logo{
        border-right: none;
    }
}



/* ****************************** */


.sub_nav_1{
    background-color: #616161;
    color: white;
    padding-left: 5vw;

}

.sub_nav_1_ul{
    margin-left: -30px;

}

.sub_nav_1_ul li{
    padding-top: 5px;
    font-size: 0.9em;
    display: inline-block;
    padding-right: 25px;
    cursor: pointer;
}

.sub-nav-mobile{
    display: none !important;
}

@media only screen and (max-width: 600px){
    .sub-nav-li{
        display: none !important;
    }

    .sub-nav-mobile{
        display: block !important;
        float: right;
    }
}

/* **************************************** */


.sticky{
    position: fixed;
    top: 0;
    left: 0;
}



