.departments{
    margin-top: 13vh;
    padding: 20px;
}

@media only screen and (max-width: 600px){
    .departments{
        margin-top: 10vh;
    }

    .dept-label{
        font-size: 1.3em !important;
    }

    .dept-section-name p{
        font-size: 1em !important;
    }

    .departments-list{
        grid-template-columns: 1fr !important;
        padding: 10px 15px 10px 15px !important;
    }
    
}

.departments header{
    text-align: center;
}

.departments header h1{
    font-size: 3em;
    font-weight: 900;
}


.departments-list{
    padding-top: 10px;
    padding-left: 60px;
    padding-right: 60px;
    padding-bottom: 50px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.department-cards{
    border: solid thin lightgrey;
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
    background-color: white;
    position: relative;
    min-height: 55vh;
}

.department-image-header{

}

.department-image-header img{
    width: 100%;
}

.department-cards header{
    text-align: left;
    padding-left: 20px;
    
}

.department-cards header h3{
    font-weight: 700;
    font-size: 1em;
    text-transform: uppercase;
}


.dept-label{
    font-size: 1.8em;
    font-style: italic;
    font-weight: 400;
}

.dept-section-name{
    padding: 20px;
    text-align: left;
    padding-left: 4.5vw;
}

.dept-section-name p{
    font-size: 1.3em;
    font-weight: 300;
}


/* ******************************************** */

.department-card-footer{
    position: absolute;
    bottom: 0;
    color: #0071c5;
    font-size: 0.9em;
    padding-bottom: 9px;
    font-weight: 600;
    padding-left: 20px;
}


