.history-full{
    margin-top: 16vh;
    margin-right: 15vw;
    margin-left: 15vw;
    padding: 10px;
    line-height: 1.5em;
}

@media only screen and (max-width: 600px){
    .history-full{
        margin-right: 5vw !important;
        margin-left: 5vw !important;
    }
    
}

.history-full h1{
    text-align: center;
    font-size: 2.4em;
    font-weight: 900;
    padding-bottom: 20px;
}

.history-full p{
    font-size: 1.4em;
    font-weight: 300;
    text-align: justify;
    padding-bottom: 20px;
    line-height: 2em;
}

.history-image{
    width: 100%;
    padding-bottom: 20px;
}



