.pdf-view{
    border: solid thin lightgray;
    overflow: hidden !important;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    padding-top: 10px;
    -webkit-transform: scale(0.7);
        -ms-transform: scale(0.7);
            transform: scale(0.7);
    margin: -170px 60px 0px 60px;
    min-height: 200vh;
}

@media only screen and (max-width: 600px){
    .pdf-view{
        margin: -170px -80px 0px -80px !important;
        
    }
}

.pdf-view div{
    width: 100%;
    text-align: center;
    overflow-x: scroll
}

.pdf-view p{
    text-align: center;
    font-weight: 700;
    font-style: normal;
    font-size: 0.8em
}
.pager{
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 20px;
    -ms-flex-pack: center;
        justify-content: center
}

.previous, .next{
    border: none;
    background-color: lightgray;
    font-size: 0.8em;
    padding: 10px;
  
}

.previousinput{
    border: solid thin lightgrey;
    padding-top: 2px;
    padding: 2px;
    padding-left: 4px;
    font-size: .6em;
    text-align: center
    
}

.previous.disabled{
    opacity: 0.3;
}

.next.disabled{
    opacity: 0.3;
}


