.form{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    padding: 10px;
}

.form-group{
    padding: 10px;
    padding-bottom: 20px;
}

.form-group label{
    font-weight: bold;
    display: block;
}

.form-group [type=text]{
    padding: 10px;
    width: 90%;
    border: solid thin #f2f2f2;
    border-radius: 2px;
}


