.mobile-nav{
    position: fixed;
    top: 0;
    right: 0;
    margin-top: 8vh;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0,0,0,0.4);
    z-index: 20;
}

.mobile-nav-container{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.mobile-nav-container div{
    font-size: 1.3em;
    font-weight: 500;
}

.close-mobile{
    border-bottom: solid thin lightgrey;
    text-align: left;
    padding-left: 40px;
    font-weight: 900;
}

.list-ul-mobile{
    text-align: left;
    display: grid;
    grid-template-columns: 1fr 1fr;
}
.list-ul-mobile>div{
    font-size: 0.8em;
    text-align: center;
    background-color: white;
    border: solid thin lightgrey;
    padding: 10px;
}


.login-mobile{
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 90vw;
    padding: 20px;
    grid-row-gap: 20px;
    grid-column-gap: 20px;
}

.login-mobile-button{
    border: none;
    background-color: lightgrey;
    padding: 5px;
    font-size: 0.8em;
}

.bottom-bar-mobile{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    background-color: #CADEFA;
    padding: 15px;
}

.bottom-bar-mobile>div{
    font-size: 0.6em;
    text-align: center;
    color: #2f2f2f;
}


