.student-card{
    width: 100%;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr;
    margin-bottom: 20px;
    -webkit-box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
    position: relative;
}

.card-top{
    background-color: black;
    height: 30vh;
    background-image: url('./../../assets/banner-card.png');
    background-size: cover;
}


.card-image{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin-top: 17vh;
    margin-left: 25vw;
    
}
.card-image-1{
    width: 30%;
    border-radius: 50%;
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.3);
            box-shadow: 0px 0px 10px rgba(0,0,0,0.3);
}

.card-text{
    padding: 20px;
    position: relative;
}

.text-blue{
    color: #0071c5;
}

.present-aecsu{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    padding: 20px;
}

.card-footer{
    text-align: center;
    padding: 10px;
    border-top: solid thin lightgrey;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
}



