.timeline{
    margin-top: 16vh;
    margin-right: 15vw;
    margin-left: 15vw;
    padding-bottom: 60px;
    line-height: 1.5;
}


.timeline-image{
    width: 100%;
}

.timeline header{
    text-align: center;
    padding-bottom: 20px;
}

.timeline header h1{
    font-size: 2.4em;
    font-weight: 900;
}

.timeline header p{
    font-size: 1.4em;
    line-height: 1.5;
}

.timeline-contents{
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}


.timeline-card{
    border: solid thin lightgrey;
    min-height: 30vh;
    -webkit-box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
    border-top: solid thick blue;
}

.timeline-card header h2{
    font-size: 1.3em;
    font-weight: 400;
}

.timeline-card-contents{
    text-align: center;
}


.visit{
    padding: 5px 20px 5px 20px;
    background-color: white;
    border: solid thin lightgrey;
}


/* ********************************************* */

.table{
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;

}

.table thead tr{
    background-color: #519DFF;
    text-align: left;
    color: white;
    padding-left: 20px;
    height: 45px;
    
}

.table tbody tr td{
    padding-left: 20px;
}

.table tbody tr{
    height: 30px;
    text-align: left;
    padding-left: 20px;
}

.table tbody tr td{
    padding: 10px;
    cursor: pointer;
}

.table tbody tr:nth-child(odd){
    background-color: #f2f2f2;
}



