.notification-card{
    background-color: #f8f8f8;
    padding: 20px;
    border-radius: 5px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 30px;
}

.general-notice-board{
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 20px;
}


.tagg{
    background-color: #f1f1f1;
    border-radius: 5px;
    padding: 10px;
}


