.banner-office{
    margin-right: 5vw;
    margin-left: 5vw;
    padding: 10px;
    line-height: 1.5;
}

@media only screen and (max-width: 600px){
    .banner-office{
        margin-right: 1vw !important;
        margin-left: 1vw !important;
    }
}


.office-image{
    padding-top: 20px;
    width: 100%;
}

.office{
    padding-top: 10px;
    display: grid;
    grid-template-columns: 2fr;
    grid-column-gap: 20px;
}

.office-contents{
    padding: 40px 0px 40px 0px;
}

.office-contents header h2{
    font-size: 2.44em;
    font-weight: 500;
    font-style: italic;
}

.office-contents p{
    line-height: 2;
    font-size: 1.3em;
    text-align: justify;
    font-weight: 300;
}

@media only screen and (max-width: 600px){
    .office-contents header h2{
        font-size: 1.5em;
    }
    .office-contents p {
        font-size: 1em !important;
    }

    .office-contents ul li{
        font-size: 1em !important;
    }
}

.office-contents ul{
    padding-top: 30px;
    margin-left: -40px;

}

.office-contents ul li{
    font-weight: 400;
    font-size: 1.3em;
    font-weight: 300;
}

.office-sidebar h2{
    font-weight: 400;
    font-style: italic;
}

hr{
    border-top : 1px solid lightgrey;
    margin-top: 5px;
    margin-bottom: 5px;
}

.office-sidebar-comp{
    display: grid;
    grid-template-columns: 1fr 3fr;
    
    margin-bottom: 10px;
    cursor: pointer;
    -webkit-box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
}

.office-sidebar-icon{
    text-align: center;
    padding: 10px;
    background-color: #f2f2f2;
}

.office-sidebar-content{
    padding: 10px;
    border: solid thin #f2f2f2;
}

.office-sidebar-content h5{
    font-size: 1em;
}

.office-sidebar-content{
    font-size: 0.8em;
}


/* ******************************************************** */

@media only screen and (max-width: 600px){
    .about-container{
        padding-left: 3vh !important;
        padding-right: 3vh !important;
    }
    .about-container header{
        font-size: 1em !important;
    }

    .about-container p{
        font-size: 1em !important;
    }
}

.about-container{
    padding: 10px;
    padding-left: 20vh;
    padding-right: 20vh;
}

.about-container header{
    font-size: 1.3em;
    color: #2f2f2f;
    text-align: left;
    padding-bottom: 20px;
    font-style: italic;
}

.about-container header h1{
    font-weight: 500;
}

.about-container header h2{
    font-weight: 600;
    font-size: 2em;
    text-align: center;
    padding: 20px;
}

.about-container header p{
    font-weight: 400;
    font-size: 0.8em;
    text-align: center;
    font-style: normal;
}

.about-container p{
    text-align: justify;
    font-size: 1.3em;
    font-weight: 300;
    line-height: 1.7em;
    padding-bottom: 30px;
}


/* ******************************* */

.office-content{
  padding: 60px 0px 60px 0px;
  display: grid;
  grid-template-columns: repeat(4,1fr);
  text-align: center;
}


/* ************************************************ */

.padding-left-3{
    padding-left: 30px;
}
@media only screen and (max-width: 600px){
    .padding-left-3{
        padding-left: 0px;
    }
}


