.side-nav-pointer:hover{
  -webkit-transform:scale(1.5);
      -ms-transform:scale(1.5);
          transform:scale(1.5);
  -webkit-transition:0.3s;
  -o-transition:0.3s;
  transition:0.3s;
  cursor:pointer;
  color:#00aeef;
}


.side-navigation1{
  position:absolute;
  z-index:1;
  font-size:20px;
  background:-webkit-gradient(linear, right top, left top, from(rgba(235,239,255,0)), color-stop(1%, rgba(235,239,255,0.01)), to(rgba(18,18,18,1)));
  background:-webkit-linear-gradient(right, rgba(235,239,255,0) 0%, rgba(235,239,255,0.01) 1%, rgba(18,18,18,1) 100%);
  background:-o-linear-gradient(right, rgba(235,239,255,0) 0%, rgba(235,239,255,0.01) 1%, rgba(18,18,18,1) 100%);
  background:linear-gradient(270deg, rgba(235,239,255,0) 0%, rgba(235,239,255,0.01) 1%, rgba(18,18,18,1) 100%);
  height:90vh;
  overflow-y: hidden;
  top:14vh;
}
.side-navigation1>ul{
  -ms-flex-item-align:center;
      align-self:center;
  list-style:none;
  margin-top:7vh;
  color:#f3f3f3;
}
.side-navigation1>ul>li{
  padding: 35px;
