  grid-template-columns: repeat(6, 1fr);
  width: 100%;
  background-color: white;
  color: white;
  padding: 20px;
  text-align: left;

}

@media only screen and (max-width: 600px){
  footer{
    grid-template-columns: 1fr;
    grid-row-gap: 20px;
  }

  footer>div>label{
    font-size: 16pt !important;
  }

  footer>div>ul>li{
