}


/***********************Portal************************/


.portal-body{
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-template-areas:
  "header-portal header-portal"
  "sidebar-portal body-portal";

}

.header-portal{
  grid-area: header-portal;
  height: 8vh;
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  width: 100vw;
  background-color:white;
  -webkit-box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
}


.logo-name{
  color: #4285f4;
  font-weight: bold;
  font-size: 14pt;
  padding: 15px 0px 0px 40px;
}



/***********************************************/

.sidebar-portal{
  grid-area: sidebar-portal;
  position: absolute;
  top: 0;
  margin-top: 8vh;
  width: 20vw;
  margin-left: -40px;
  border-right: solid thin lightgray;
  min-height: 100vh;
}

.sidebar-ul{
  padding-top: 10px;
  list-style-type: none;
}

.sidebar-li{
  padding: 15px 0px 15px 20px;
  cursor: pointer;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.sidebar-li div{
  
  display: inline;
  -ms-flex-pack: center;
      justify-content: center;

}

.sidebar-li i{
  padding-right: 10px;
}
.sidebar-li:hover{
  background-color: #f3f3f3;
  color: #4285f4;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}


/* ******************** */

.portal-body-inner{
  grid-area: body-portal;
  margin-top: 10vh;
}

/* ****************************** */


.weather{
  height: 13vh;
  background-color: #FFF3F3;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
}

@media only screen and (max-width: 600px){
  .temperature{
    font-size: 1em !important;
  }
}

.weather header{
  padding-left: 20px;
}

