}

.temperature{
  padding-left: 20px;
  font-size: 1.4em;
  color: rgb(83, 83, 83);
  font-weight: 300;
  -ms-flex-preferred-size: 80%;
      flex-basis: 80%;
}


/* ********************************** */


.tagline-text{
  font-size: 1.2em !important;
  font-weight: 300;
  line-height: 1.8em !important;
}


#full_length{
  min-height: 60vh;
}


/* ************************************ */

.f-news-banner{
  padding: 30px;
  overflow-y: hidden;
}

.f-news-image{
  width: 100%;
}


