.about-banner{
    
    padding: 20px;
}

.about-banner img{
    width: 100%;
}


.about-full{
    margin-top: 16vh;
    padding: 20px 40px 0px 70px; 
    line-height: 1.5;
    
}

.about-full header h1{
    font-size: 2.4em;
    font-weight: 900;
    text-align: center;
}

.about-full header p {
    font-size: 1.5em;
    text-align: center;
}


.about-sh{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-top: 30px;
    padding-bottom: 30px;
}

@media only screen and (max-width: 600px){
    .about-sh{
        grid-template-columns: 1fr;
        grid-row-gap: 30px;
    }
    .about-sh div{
        font-size: 0.9em !important;
    }
}
.about-sh div{
    text-align: center;
    font-size: 1.2em;
}

.about-sh div span{
    font-weight: 900;
    font-size: 1.8em;
}


#about-sh-one{
    border-right: solid thin lightgrey;
}

@media only screen and (max-width: 600px){
    #about-sh-one{
        border-right: none;
        font-size: 0.9em !important;
    }
}

.aboutus-sections h1{
    font-size: 2.4em;
    font-weight: 900;
}



.mission-vision{
    
    text-align: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "vision mission";
    grid-column-gap: 90px;
}

.vision{
    grid-area: vision;
    padding: 10px 60px 20px 60px; 
    /* border: 1px solid lightgrey; */
    
}

.vision i{
    color: #0071c5;
}

.mission{
    grid-area: mission;
    padding: 10px 60px 20px 60px; 
    /* border: 1px solid lightgrey; */
}

@media only screen and (max-width: 600px){
    .mission-vision{
        grid-template-columns: 1fr;
        grid-template-areas: "vision"
                            "mission";
        grid-row-gap: 30px;
    }

    .vision{
        padding:  0px !important; 
    }

    .mission{
        padding:  0px !important; 
    }
}


.mission i{
    color: #0071c5;
}

.bg-light{
    background-color: #F3F3F3;
    text-align: center;
    padding: 40px 40px 50px 70px; 
    min-height: 73vh;
}


.bg-light h1{
    font-size: 2.4em;
    font-weight: 900;
}

.aboutus-sections{
    /* padding: 20px 40px 0px 70px;  */
    text-align: center;
    /* height: 112vh; */
}


.history{
    padding: 50px 90px 50px 90px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 40px;
}

@media only screen and (max-width: 600px){
    .history{
        padding: 10px !important;
        grid-template-columns: 1fr;
        grid-row-gap: 30px;
    }
}

.history div{
   /*  box-shadow: 1px 1px 10px rgba(0,0,0,0.1); */
   padding-bottom: 20px;
   text-align: justify;
   padding: 10px;
}

.history div h3{
    text-align: left;
    padding-left: 20px;
    padding-top: 10px;
}

.history div img{
    width: 100%
}

.history div p{
    padding: 10px;
    font-size: 1.2em;
    font-weight: 300;
}


.btn-default{
    background-color: lightgrey;
    border: none;
    padding: 10px;
    font-size: 1.1em;
}

.btn-default:hover{
    -webkit-box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 20px rgba(0,0,0,0.1);
}

.tour{
    background-color: #F9F6EF;
    text-align: center;
    -ms-flex-pack: center;
        justify-content: center;
    min-height: 300px;
    font-weight: 100;
    padding-top: 100px;
}


.tour h1{
    font-size: 2.4em;
    font-weight: 900;
}

.btn-tour{
    background: none;
    border: solid thin lightgrey;
    padding: 10px 30px 10px 30px;
    font-size: 1.2em;
}


.btn-tour:hover{
    background-color: #2F2424;
    color: white;
}



.leadership{
    padding: 50px 90px 50px 90px;
    min-height: 108vh;
    overflow-y: hidden;
}



.leadership header h1{
    font-size: 2.4em;
    font-weight: 900;
    text-align: center;
}

.leadership-grid{
    padding-top: 30px;
    padding-bottom: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 30px;
    
}

@media only screen and (max-width: 600px){
    .leadership{
        padding: 10px 50px 10px 50px;
    }
    .leadership-grid{
        grid-template-columns: 1fr;
        grid-row-gap: 40px;
    }
    
}

.lead-image{
    width: 100%;
}


.leadership-grid div h2{
    font-weight: 600;
    font-size: 1.1em;
}

.leadership-grid div p{
    padding-top: 7px;
    text-align: justify;
    font-size: 0.9em;
    line-height: 1.5;
    padding-bottom: 10px;
}

.lead-button{
    font-size: 1em;
    color: #0071c5;
    font-weight: 900;
}

.lead-button:hover{
    color: #0071c5;
}

/* ************************* */



.glimpses-text{
    font-size: 1.3em;
    line-height: 2em;
    font-weight: 300;
    font-style: italic;
    text-align: justify;
    padding: 60px;
    padding-left: 8vw;
    padding-right: 8vw;
    padding-bottom: 15vh;
}

@media only screen and (max-width: 600px){
    .glimpses-text{
        font-size: 0.9em !important;
    }
}

.vision-text{
    font-size: 1.5em !important;
    font-weight: 300;
    font-style: italic;
    text-align: justify;
}

.mission-text{
    font-size: 1.5em !important;
    font-weight: 300;
    font-style: italic;
    text-align: justify;
    padding-bottom: 15px;
}

.bgsuperlight{
    background-color: #f9f9f9;
}


