.teacher-card{
    width: 100%;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr;
    margin-bottom: 20px;
    -webkit-box-shadow: 2px 2px 10px rgba(0,0,0,0.1);
            box-shadow: 2px 2px 10px rgba(0,0,0,0.1);
    position: relative;
    border: solid thin lightgrey;
}

.card-top-t{
    min-height: 15vh;
    text-align: center;
    padding: 20px;
    overflow-y: hidden;
}


.card-image-t{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    margin-top: 2vh;
    margin-left: 25vw;
    
}
.card-image-1-t{
    width: 130px;
    border-radius: 50%;
}

.card-text-t{
    padding: 20px;
    position: relative;
    height: 35vh;
}

.text-blue{
    color: #0071c5;
}


.card-footer-t{
    text-align: center;
    padding: 10px;
    border-top: solid thin lightgrey;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    background-color: #f9f9f9;
}



