.notification-home{
    margin-top: 20vh;
}

.notification-home header{
    text-align: center;
}

.notification-home header h1{
    font-size: 2.44em;
    font-weight: 900;
    padding-bottom: 20px;
}


.notification-home header p{
    font-size: 1.5em;
    padding-left: 20vw; 
    padding-right: 20vw; 
}

.notification-grid{
    padding: 10px 40px 10px 40px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-areas: "tend noti";
    grid-column-gap: 30px;
}

@media only screen and (max-width: 600px){
    .notification-home{
        margin-top: 10vh;
    }
    .notification-grid{
        padding: 10px 15px 10px 15px;
        grid-template-columns: 1fr;
        grid-template-areas: "noti" "tend";
        grid-row-gap: 30px;
    }
}

.notification-tender{
    background-color: white;
}

.notification-tender header{
    background-color: rgb(184, 184, 184);
    padding: 17px;
    color: white;
    border-radius: 5px;
}

.notification-main{
    border: solid thin lightgrey;
    -webkit-box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
            box-shadow: 0px 0px 30px rgba(0,0,0,0.2);
    background-color: #f2f2f2;
}

#tend {
    grid-area: tend;
    
}

#noti{
    grid-area: noti;
}


.notification-main header{
    padding: 10px;
    color: white;
    text-align: left;
    background-color: #3EAF67  ;    
}

.notification-main header h2{
    font-size: 1.8em;
    font-weight: 700;
}

.notification-tender-contents{
    padding: 10px;
}


.notification-main-contents{
    padding: 10px;
}


.tender-ul{
    margin-left: -40px;
}
.tender-ul li{
    padding: 20px;
    font-weight: 600;
    border: solid thin lightgrey;
    background-color: white;
    margin-bottom:5px;
    cursor: pointer;
}


.main-ul{
    margin-left: -40px;
}
.main-ul li{
    padding: 15px;
    background-color: white;
    border: solid thin lightgrey;
    margin-bottom:10px;
    cursor: pointer;
}

.main-ul li button{
    margin-top: 15px;
    font-size: 0.7em;
    background-color: #d8d8d8;
    border: none;
    padding: 5px;
    border-radius: 3px;
    
}

.tender-ul li button{
    margin-top: 15px;
    font-size: 0.7em;
    background-color: #d8d8d8;
    border: none;
    padding: 5px;
    border-radius: 3px;
    
}

.download-button{
    display: inline-block;
    margin-top: 15px;
    font-size: 0.7em;
    background-color: #d8d8d8;
    border: none;
    padding: 5px;
    border-radius: 3px;
    
}

.notification-tender-contents{
    padding: 10px;
}

.notification-tender-subscribe{
    padding: 10px;
    background-color: white;
}

.postedon{
    font-size: 0.7em;
    color: green;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.full-notification{
    padding: 10px;
}

.full-notification header{
    text-align: left;
}

.full-notification header h2{
    font-size: 2em;
}

.full-contents{
    padding-top: 20px;
}

.full-contents p{
    text-align: justify;
    line-height: 2em;
}


