.landing-sections{
    margin-top: 20vh;
    padding-left: 15vh;
    padding-right: 15vh; 
}

@media only screen and (max-width: 600px){
    .landing-sections{
        margin-top: 10vh !important;
        padding-left: 3vh !important;
        padding-right: 3vh !important;
    }

    .hostel-desc p{
        font-size: 1em !important;
    }
}

.landing-sections header{
    text-align: center;
}

.landing-sections header h1{
    font-size: 2.44em;
    font-weight: 900;
}

.hostellist-grid{
    padding: 10px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    margin-bottom: 5vh;
}

@media only screen and (max-width: 600px){
    .hostellist-grid{
        grid-template-columns: 1fr;
    }
}

.hostelcard{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    border: solid thin lightgrey;
    -webkit-box-shadow: 0px 0px 15px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 15px rgba(0,0,0,0.1);
}

.hostelcard-image{
    width: 100%;
}

.hostelcard header{
    text-align: left;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 10px;
}

.hostelcard-tagline{
    padding-left: 10px;
}

.hostelcard-footer{
    background-color: #f2f2f2;
    padding: 10px;
    font-size: 0.9em;
    font-weight: 700;
}

.float-right{
    float: right;
}


.hostel-desc{
    padding: 10px;
}

.hostel-desc p{
    text-align: justify;
    line-height: 2em;
    font-size: 1.2em;
    font-weight: 300;
    margin-bottom: 5vh;
}


