.news-list-grid{
    padding: 20px;
    display: grid;
    grid-template-columns: 5fr;
    grid-column-gap: 20px;
}


.news-list-list{
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 10px;
}

.short-news{
    background-color: #f9f9f9;
    padding: 10px;
    border-radius: 5px;
    -webkit-box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
            box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
}


.full-news{
    padding-left: 20px;
    padding-right: 20px;
}

.full-news h3{
    font-size: 2em;
}

.full-news h6{
    font-size: 1em;
}

.full-news p{
    font-size: 1.3em;
    padding-top: 20px;
    font-weight: 300;
    text-align: justify;
}


