.tlist{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    padding: 10px;
}


@media only screen and (max-width: 600px){
    .tlist{
        grid-template-columns: 1fr;
    }
}
.department-body-home-grid{
    display: grid;
    grid-template-columns: 3fr;
    grid-column-gap: 20px;
}

.department-banner{
    width: 100%;
}

.cards-dept-hod{
    display: grid;
    grid-template-areas: "header-hod header-hod"
        "image-hod text-hod";
}

.header-hod{
    grid-area: header-hod;
}

.image-of-hod{
    grid-area: image-hod;
}

.cards-contents-hod{
    grid-area: text-hod;
}

.hod-image{
    width: 100%;
}

.description-department{
    padding-top: 10px;
    padding-bottom: 10px;
    font-style: italic;
}

.about-content-single{
    display: grid;
    grid-template-columns: 3fr;
    text-align: center;
    padding: 60px 0px 60px 0px;
}

.about-content-single div{
  padding: 20px;
  cursor: pointer;
  font-size: 1.5em;
}


.about-content-single div>span{
  font-size: 2em !important;
  font-weight: 900;
}



