.subnav-mobile{
    position: absolute;
    z-index: 20;
    top: 0;
    left: 0;
    width: 100vw;
    margin-top: 25vh;
    
}

.sub-nav-grid{
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: #616161;
}

.sub-nav-grid div{
    text-align: center;
    border: solid thin lightgrey;
    padding: 5px;
}


