.blog-card{
    border-radius: 5px;
    border: solid thin lightgrey;
    border-top: solid thick #0071c5;
    padding: 20px;
    box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
    -webkit-box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
    -moz-box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
}

.blog-title h1{
    color: brown;
    font-size: 1.7em;
}


